.dd-wrapper {
    display: flex;
    /* min-height: 38px; */
    flex-wrap: wrap;
    flex-direction: column;
}
.dd-wrapper .dd-header {
    background-color: white;
    border-color: #ccc;
    border-radius: 4px;
    border-style: solid;
    border-width: 1px;
    box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, .075) !important;
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    width: 100%;
    padding: 0 20px;
}
.dd-wrapper .dd-header__title--bold {
    font-weight: bold;
}
.dd-wrapper .dd-list {
    background-color: rgba(70, 70, 70, 0.849);
    border-radius: 5%;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    padding: 0;
    justify-content: center;
    position: absolute;
    margin-top: 20px;
    margin-bottom: 5%;
}

.dd-wrapper .dd-list li {
    list-style-type: none;
    padding: 0 50px 0 0;
    margin-left: 20px;
    margin-top: 1%;
    margin-bottom: 5%;
}
.dd-wrapper .dd-list li:first-of-type > button {
    border-top: 1px solid #ccc;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
}
.dd-wrapper .dd-list li:last-of-type > button {
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
}
.dd-wrapper .dd-list li button {
    display: flex;
    justify-content: space-between;
    background-color: white;
    font-size: 16px;
    padding: 15px 20px 15px 20px;
    border: 0;
    border-bottom: 1px solid #ccc;
    width: 100%;
    text-align: left;
    border-left: 1px solid #ccc;
    border-right: 1px solid #ccc;
}
.dd-wrapper .dd-list li button:hover, .dd-wrapper .dd-list li button:focus {
    cursor: pointer;
    font-weight: bold;
    background-color: #ccc;
}
